.home {
    padding: 30px;
    box-sizing: border-box;
    width: calc(100vw - 180px);
    /* max-width: calc(100% - 160px); */

    display: flex;
    flex-direction: column;
}

/* 申请模块 */
.top_bottom {
    width: 100%;
    height: 200px;
}


/* 底部卡片 */
.bottom-content {
    width: 100%;
    margin-top: 20px;
    display: flex;
}

.left {
    width: 60%;

}

.right {
    margin-left: 2%;
    width: 38%;
}

@media screen and (max-width: 1248px) {

    /* 可以在此设置超小屏幕样式 */
    .top_bottom {
        width: 90%;
        height: 500px;
        min-width: 550px;
      
    }

    .bottom-content {
        width: 90%;
        min-width: 550px;


    }
}

@media screen and (min-width:1800px) {
    .top_bottom {
        width: 100%;
        height: 25vh;
        min-height: 300px;
        display: flex;
        flex-direction: column;
    }

}