.apply {
    width: 100%;
    height: 200px;
    border-radius: 18px;
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 15px;
}

h3 {
    font-weight: 400;
    font-size: 20px;
}

.show {
    width: 100%;
    height: 120px;
    margin-top: 15px;
    flex-wrap: wrap;
    display: flex;
    align-items: center;
    justify-content: center;
}

.show_content {
    width: 15%;
    height: 100px;
    margin-right: 5%;
    position: relative;
}

.show_name {
    position: absolute;
    top: 25%;
    right: 10%;
}

.show_name h3 {
    color: #999999;
}

.all {
    background-image: url('../../../assest/images/all.jpg');
    background-size: cover;
}

.wait {
    background-image: url('../../../assest/images/wait.jpg');
    background-size: cover;
}

.wait_check {
    background-image: url('../../../assest/images/wait_check.jpg');
    background-size: cover;
}

.disagree {
    background-image: url('../../../assest/images/disagree.jpg');
    background-size: cover;
}

.agree {
    background-image: url('../../../assest/images/agree.jpg');
    background-size: cover;
}

@media screen and (max-width: 1248px) {

    /* 可以在此设置超小屏幕样式 */
    .apply {
        width: 100%;
        height: 500px;
    }

    .show {
        width: 100%;
        height: 400px;
        margin-top: 15px;
        padding-left: 30px;
        display: grid;
        grid-template-columns: 50% 50%;
        grid-column-gap: 25px;
    }

    .show_content {
        width: 60%;
        height: 100px;

        position: relative;
    }

    .show_name {
        position: absolute;
        top: 25%;
        right: 3%;
    }
}

@media screen and (min-width:1800px) {
    .apply {
        width: 100%;
        height: 100%;
        min-height: 300px;
        display: flex;
        flex-direction: column;
    }

    .show {
        height: 80%;
    }

    .show_content {
        height: 100%;
    }

    .show_name {

        top: 40%;
        right: 5%;
    }

    .show_name h3 {
        font-size: 26px;
    }

    .show_name h1 {
        font-size: 32px;
    }
}